<template>
  <div>
    <!-- مرحله اول: ارسال شماره موبایل -->
    <form v-if="step === 1" @submit.prevent="submitPhone">
      <label for="phone">شماره تلفن:</label>
      <input
        id="phone"
        v-model="phone"
        type="text"
        placeholder="مثلاً 09123456789"
        required
      />
      <button type="submit">ارسال</button>
      <p v-if="message">{{ message }}</p>
    </form>

    <!-- مرحله دوم: وارد کردن کد پیامکی -->
    <form v-else-if="step === 2" @submit.prevent="submitCode">
      <label for="code">کد پیامک شده:</label>
      <input
        id="code"
        v-model="code"
        type="text"
        placeholder="کد تأیید"
        required
      />
      <button type="submit">تأیید کد</button>
      <p v-if="message">{{ message }}</p>
    </form>
  </div>
</template>

<script setup>
import { sendApi } from '@/utils/api'
import { ref } from 'vue'

const phone = ref('')
const code = ref('')
const message = ref('')
const step = ref(1) // مرحله‌ی جاری: 1 = شماره، 2 = کد

const submitPhone = async () => {
  try {
    const response = await sendApi(
      JSON.stringify({
        action: 'send_phone_login',
        data: phone.value,
      })
    );
    if (response.status === 'success') {
      message.value = 'شماره با موفقیت ارسال شد. لطفاً کد پیامکی را وارد کنید.'
      step.value = 2 // برو به مرحله بعد
    } else {
      alert(response.message || 'ارسال موفق نبود')
    }
  } catch (error) {
    message.value = 'خطا در ارسال شماره.'
    console.error(error)
  }
}

const submitCode = async () => {
  try {
    const response = await sendApi(
      JSON.stringify({
        action: 'verify_sms_code',
        data: {
          phone: phone.value,
          code: code.value,
        },
      })
    )
    if (response.status === 'verified') {
      message.value = 'ورود با موفقیت انجام شد!'
      // ادامه مسیر مثل ریدایرکت یا تغییر وضعیت لاگین
    } else {
      message.value = 'کد وارد شده اشتباه است.'
    }
  } catch (error) {
    message.value = 'خطا در تأیید کد.'
    console.error(error)
  }
}
</script>

<style scoped>
form {
  max-width: 300px;
  margin: auto;
}
label, input, button {
  display: block;
  width: 100%;
  margin-bottom: 10px;
}
</style>
